import React, { useState } from 'react';
import Image from 'next/image';
import StarterMinerModal from './StarterMinerModal';

interface FacilityData {
  power: number;
  level: number;
  miners: number;
  capacity: number;
  used: number;
  resources: number;
  spaces: number;
}

interface RoomVisualizationProps {
  hasFacility: boolean;
  facilityData?: FacilityData;
  onPurchaseFacility: () => Promise<void>;
  onGetStarterMiner: (x: number, y: number) => Promise<void>;
  onUpgradeFacility: () => Promise<void>;
  isPurchasingFacility: boolean;
  isGettingStarterMiner: boolean;
  isUpgradingFacility: boolean;
  onTileSelect?: (x: number, y: number) => void;
}

const RoomVisualization: React.FC<RoomVisualizationProps> = ({
  hasFacility,
  facilityData,
  onPurchaseFacility,
  onGetStarterMiner,
  onUpgradeFacility,
  isPurchasingFacility,
  isGettingStarterMiner,
  isUpgradingFacility,
  onTileSelect
}) => {
  const [isGridMode, setIsGridMode] = useState(false);
  const [selectedTile, setSelectedTile] = useState<{x: number, y: number}>();
  const [isStarterMinerModalOpen, setIsStarterMinerModalOpen] = useState(false);

  const handleTileClick = (x: number, y: number) => {
    if (!isGridMode) return;
    setSelectedTile({ x, y });
    setIsStarterMinerModalOpen(true);
    if (onTileSelect) {
      onTileSelect(x, y);
    }
  };

  const handleStarterMinerClaim = async (x: number, y: number) => {
    await onGetStarterMiner(x, y);
    setIsStarterMinerModalOpen(false);
    setIsGridMode(false);
    setSelectedTile(undefined);
  };

  if (!hasFacility) {
    return (
      <div className="room-container flex flex-col items-center justify-center h-[500px] relative">
        <h2 className="pixel-text text-2xl mb-8">YOU DONT HAVE A SPACE TO MINE!</h2>
        <button 
          className="pixel-button"
          onClick={onPurchaseFacility}
          disabled={isPurchasingFacility}
        >
          {isPurchasingFacility ? 'PURCHASING...' : 'BUY FACILITY'}
        </button>
      </div>
    );
  }

  return (
    <div className="room-container relative h-[500px] border-2 border-banana">
      {/* Bedroom Facility Background */}
      <div className="absolute inset-0 flex items-center justify-center">
        <div className="relative w-[600px] h-[400px]">
          <Image
            src="/bedroom.png"
            alt="Mining Facility"
            fill
            className="object-contain"
            priority
          />

          {/* Grid Spaces - The actual blue boxes in the image */}
          <div className="absolute inset-0">
            {/* Top Left - X:0, Y:0 (Near Bed) */}
            <div 
              onClick={() => handleTileClick(0, 0)}
              className={`absolute top-[45%] left-[30%] w-[60px] h-[60px] cursor-pointer transition-all duration-300 ${
                isGridMode ? 'hover:bg-banana hover:bg-opacity-20' : ''
              } ${
                selectedTile?.x === 0 && selectedTile?.y === 0 
                  ? 'animate-pulse bg-banana bg-opacity-30' 
                  : ''
              }`}
            />

            {/* Top Right - X:1, Y:0 (Near Bananas) */}
            <div 
              onClick={() => handleTileClick(1, 0)}
              className={`absolute top-[45%] right-[30%] w-[60px] h-[60px] cursor-pointer transition-all duration-300 ${
                isGridMode ? 'hover:bg-banana hover:bg-opacity-20' : ''
              } ${
                selectedTile?.x === 1 && selectedTile?.y === 0 
                  ? 'animate-pulse bg-banana bg-opacity-30' 
                  : ''
              }`}
            />

            {/* Bottom Left - X:0, Y:1 (Near Jukebox) */}
            <div 
              onClick={() => handleTileClick(0, 1)}
              className={`absolute bottom-[20%] left-[30%] w-[60px] h-[60px] cursor-pointer transition-all duration-300 ${
                isGridMode ? 'hover:bg-banana hover:bg-opacity-20' : ''
              } ${
                selectedTile?.x === 0 && selectedTile?.y === 1 
                  ? 'animate-pulse bg-banana bg-opacity-30' 
                  : ''
              }`}
            />

            {/* Bottom Right - X:1, Y:1 (Near UI) */}
            <div 
              onClick={() => handleTileClick(1, 1)}
              className={`absolute bottom-[20%] right-[30%] w-[60px] h-[60px] cursor-pointer transition-all duration-300 ${
                isGridMode ? 'hover:bg-banana hover:bg-opacity-20' : ''
              } ${
                selectedTile?.x === 1 && selectedTile?.y === 1 
                  ? 'animate-pulse bg-banana bg-opacity-30' 
                  : ''
              }`}
            />
          </div>

          {/* Control Buttons - Using the actual buttons in the image */}
          <div className="absolute bottom-0 left-0 right-0 h-[80px] flex items-center justify-center gap-4">
            <button 
              onClick={() => setIsGridMode(!isGridMode)}
              className={`w-[100px] h-[40px] bg-transparent ${
                isGridMode ? 'opacity-70' : ''
              } hover:opacity-80 transition-opacity`}
              disabled={isGettingStarterMiner}
              style={{
                position: 'absolute',
                left: '30%',
                bottom: '10px'
              }}
            >
              <span className="sr-only">
                {isGettingStarterMiner ? 'CONNECTING...' : 'GRID'}
              </span>
            </button>
            <button 
              onClick={onUpgradeFacility}
              className="w-[100px] h-[40px] bg-transparent hover:opacity-80 transition-opacity"
              disabled={isUpgradingFacility}
              style={{
                position: 'absolute',
                right: '30%',
                bottom: '10px'
              }}
            >
              <span className="sr-only">
                {isUpgradingFacility ? 'UPGRADING...' : 'UPGRADE'}
              </span>
            </button>
          </div>
        </div>
      </div>

      {/* Starter Miner Modal */}
      <StarterMinerModal
        isOpen={isStarterMinerModalOpen}
        onClose={() => {
          setIsStarterMinerModalOpen(false);
          setSelectedTile(undefined);
        }}
        onClaim={handleStarterMinerClaim}
        selectedTile={selectedTile}
        isProcessing={isGettingStarterMiner}
      />
    </div>
  );
};

export default RoomVisualization;
